import mathsteps from 'mathsteps';
window.mathsteps = mathsteps;

let currentEquation = '';
let variables = new Set();
let equationHistory = [];
let inSuperscriptMode = false;
let isDecimalFormat = true;  // Add this line at the top with other variables
let currentSolution = null;  // Store the current solution for toggling
let ggbApp = null;  // Replace calculator variable with ggbApp

function loadScript(url, callback) {
    const script = document.createElement('script');
    script.src = url;
    script.onload = callback;
    script.onerror = () => {
        console.error(`Failed to load script: ${url}`);
    };
    document.head.appendChild(script);
   }

document.addEventListener('DOMContentLoaded', () => {
    initEventListeners();
    loadHistory();
    document.getElementById('toggleFormat').addEventListener('click', toggleSolutionFormat);
    testMathsteps();
    const params = {
        "appName": "graphing",
        "width": 800,
        "height": 400,
        "showToolBar": false,
        "showAlgebraInput": false,
        "showMenuBar": false,
        "showResetIcon": true,
        "enableLabelDrags": false,
        "enableShiftDragZoom": true,
        "enableRightClick": false,
        "borderColor": null,
        "capturingThreshold": null
    };
    
    ggbApp = new GGBApplet(params, true);
    window.addEventListener("load", function() {
        ggbApp.inject('ggb-element');
    });
});

function initEventListeners() {
    const input = document.getElementById('equationInput');
    const symbolButtons = document.querySelectorAll('.symbol-btn');
    
    input.addEventListener('input', handleInput);
    input.addEventListener('keypress', (e) => {
        if (e.key === 'Enter') solveEquation();
    });
    
    symbolButtons.forEach(btn => {
        btn.addEventListener('click', () => insertSymbol(btn.dataset.value));
    });

    document.querySelector('.solve-btn').addEventListener('click', solveEquation);
}
function handleInput(e) {
    const input = e.target;
    let cursorPos = input.selectionStart;
    let equation = input.value;
    
    let newEquation = "";
    let i = 0;
    let exponentDepth = 0;  // Track nested parentheses in exponents
    
    while (i < equation.length) {
        if (equation[i] === "^") {
            newEquation += "^";
            // Set exponent mode only if next char is (
            if (i + 1 < equation.length && equation[i + 1] === "(") {
                exponentDepth++;
                i++;  // Skip the next '(' as it is part of the exponent
                newEquation += "{";  // Convert '(' to '{'
            }
        } else if (exponentDepth > 0 && equation[i] === "(") {
            newEquation += "{";
            exponentDepth++;
        } else if (exponentDepth > 0 && equation[i] === ")") {
            newEquation += "}";
            exponentDepth--;
        } else {
            newEquation += equation[i];
        }
        i++;
    }
    
    // Ensure that any unmatched opening curly brackets are closed
    while (exponentDepth > 0) {
        newEquation += "}";
        exponentDepth--;
    }
    
    input.value = newEquation;
    input.setSelectionRange(cursorPos, cursorPos);
    
    currentEquation = input.value;
    validateEquation(currentEquation);
    updatePreview(currentEquation);
}



function insertSymbol(symbol) {
    const input = document.getElementById('equationInput');
    const pos = input.selectionStart;
    const newValue = input.value.slice(0, pos) + symbol + input.value.slice(pos);
    input.value = newValue;
    input.setSelectionRange(pos + symbol.length, pos + symbol.length);
    handleInput({ target: input });
    input.focus();
}

function updatePreview(equation) {
    if (!equation) {
        document.getElementById('equationPreview').innerHTML = '';
        return;
    }

    const latex = convertToLatex(equation);
    document.getElementById('equationPreview').innerHTML = `\\[ ${latex} \\]`;
    MathJax.typesetPromise();
}

function convertToLatex(equation) {
    return equation
        .replace(/\*/g, '\\cdot ')
        .replace(/sqrt/g, '\\sqrt')
        .replace(/pi/g, '\\pi ')
        .replace(/\^([a-zA-Z0-9]+)/g, '^{\u200B$1}')  // Wrap exponents without changing input field
        .replace(/sin/g, '\\sin')
        .replace(/cos/g, '\\cos')
        .replace(/tan/g, '\\tan')
        .replace(/log/g, '\\log');
}

async function solveEquation() {
    const errorMessage = document.getElementById('errorMessage');
    errorMessage.style.display = 'none';

    try {
        let equation = currentEquation
            .replace(/\^{([^}]+)}/g, '^($1)')
            .replace(/\s/g, '');
        
        const vars = new Set();
        equation.replace(/[a-zA-Z]+/g, match => {
            if (!['sin', 'cos', 'tan', 'log', 'ln'].includes(match)) {
                vars.add(match);
            }
        });

        if (vars.size > 1) {
            // Show variable selection modal
            showVariableModal(Array.from(vars), equation);
            return;
        }

        // Proceed with single variable or no variable case
        solveForVariable(equation, Array.from(vars)[0]);
    } catch (error) {
        errorMessage.textContent = `Error: ${error.message}`;
        errorMessage.style.display = 'block';
    }
}

function showVariableModal(variables, equation) {
    const modal = document.getElementById('variableModal');
    const varList = document.getElementById('variableList');
    
    // Clear previous list
    varList.innerHTML = '';
    
    // Create buttons for each variable
    variables.forEach(variable => {
        const btn = document.createElement('button');
        btn.className = 'variable-btn';
        btn.textContent = `Solve for ${variable}`;
        btn.onclick = () => {
            solveForVariable(equation, variable);
            modal.style.display = 'none';
        };
        varList.appendChild(btn);
    });
    
    modal.style.display = 'block';
    
    // Close modal when clicking outside
    modal.onclick = (e) => {
        if (e.target === modal) {
            modal.style.display = 'none';
        }
    };
}

function solveForVariable(equation, variable) {
    try {
        let solution;
        if (equation.includes('=')) {
            const [left, right] = equation.split('=');
            equation = `${left}-(${right})`;
            solution = nerdamer.solve(equation, variable);
        } else {
            equation = equation + '=0';
            solution = nerdamer.solve(equation, variable);
        }

        displaySolution(solution, variable);
    } catch (error) {
        const errorMessage = document.getElementById('errorMessage');
        errorMessage.textContent = `Error: ${error.message}`;
        errorMessage.style.display = 'block';
    }
}

function validateEquation(equation) {
    const validationMessage = document.getElementById('validationMessage');
    
    // Check for balanced brackets and braces
    const bracketBalance = equation.split('(').length === equation.split(')').length;
    const braceBalance = equation.split('{').length === equation.split('}').length;

    if (!bracketBalance || !braceBalance) {
        validationMessage.textContent = 'Unmatched brackets or braces';
        return false;
    }

    try {
        // Convert LaTeX-style exponents before parsing
        const parsedEq = equation.replace(/\^{([^}]+)}/g, '^($1)');
        const parsed = math.parse(parsedEq);
        variables.clear();
        parsed.traverse((node) => {
            if (node.isSymbolNode && !math[node.name]) {
                variables.add(node.name);
            }
        });

        validationMessage.textContent = '';
        return true;
    } catch (error) {
        return false;
    }
}

function displaySolution(solution, variable = null) {
    currentSolution = solution;
    const stepsDiv = document.getElementById('solutionSteps');
    
    let solutionText;
    if (Array.isArray(solution.symbol.elements)) {
        const solutions = solution.symbol.elements.map((elem, i) => 
            variable ? `${variable}_${i+1} = ${formatSolution(elem)}` : formatSolution(elem)
        );
        solutionText = solutions.join(',\\ ');
    } else {
        solutionText = variable ? 
            `${variable} = ${formatSolution(solution)}` : 
            formatSolution(solution);
    }

    // Generate step by step solution
    let stepsHtml = `<p>Input: \\[ ${convertToLatex(currentEquation)} \\]</p>`;
    
    // Add solution steps
    if (variable) {
        stepsHtml += generateSolutionSteps(currentEquation, variable);
    }
    
    // Add final result
    stepsHtml += `<p>Final Result: \\[ ${convertToLatex(solutionText)} \\]</p>`;
    
    stepsDiv.innerHTML = stepsHtml;
    saveToHistory(currentEquation, solutionText);
    MathJax.typesetPromise();
    
    if (variable) {
        plotGraph(currentEquation, variable);
    }
}

function generateSolutionSteps(equation, variable) {
    let steps = '';
    try {
        if (equation.includes('=')) {
            // For equations
            steps = solveEquationWithSteps(equation, variable);
        } else {
            // For expressions
            steps = simplifyExpressionWithSteps(equation);
        }
    } catch (error) {
        console.error('Error generating steps:', error);
        steps = '<p>Could not generate step-by-step solution.</p>';
    }
    return steps;
}

function testMathsteps() {
    console.log('Testing mathsteps...');
    
    // Test equation
    const equation = '2x + 3 = 7';
    console.log('Test equation:', equation);
    
    try {
        const steps = mathsteps.solveEquation(equation);
        console.log('Steps:', steps);
        
        steps.forEach((step, index) => {
            console.log(`Step ${index + 1}:`);
            console.log('  Before:', step.oldEquation?.ascii());
            console.log('  After:', step.newEquation?.ascii());
            console.log('  Change:', step.changeType);
        });
    } catch (error) {
        console.error('Mathsteps error:', error);
    }
}


function solveEquationWithSteps(equation, variable) {
    console.log('Input equation:', equation);
    let steps = '<div class="solution-steps">';
    steps += '<h4>Solution Steps:</h4>';

    try {
        // Parse equation into standard form
        const [left, right] = equation.split('=');
        const standardForm = `${left}-(${right})`;
        console.log('Standardized form:', standardForm);
        
        // Get steps from mathsteps
        const equationSteps = mathsteps.solveEquation(standardForm);
        console.log('Mathsteps output:', equationSteps);
        
        equationSteps.forEach((step, index) => {
            console.log(`Step ${index + 1}:`, {
                changeType: step.changeType,
                before: step.oldEquation?.ascii(),
                after: step.newEquation?.ascii()
            });
            
            steps += 
                `<div class="step">
                    <div class="step-number">${index + 1}</div>
                    <div class="step-explanation">${step.changeType}</div>
                    <div class="step-math">\\[ ${convertToLatex(step.newEquation.ascii())} \\]</div>
                </div>`;
        });

    } catch (error) {
        console.error('Full error details:', error);
        steps += '<p>Could not generate detailed steps.</p>';
    }

    steps += '</div>';
    return steps;
}


function simplifyExpressionWithSteps(expression) {
    let steps = '<div class="solution-steps">';
    steps += '<h4>Simplification Steps:</h4>';

    try {
        // Use mathsteps to simplify the expression
        const simplifySteps = mathsteps.simplifyExpression(expression);
        
        simplifySteps.forEach((step, index) => {
            steps += 
                `<div class="step">
                    <div class="step-number">${index + 1}</div>
                    <div class="step-explanation">${step.changeType}: ${step.substeps.join(', ')}</div>
                    <div class="step-math">\\[ ${convertToLatex(step.newNode.toString())} \\]</div>
                </div>`;
        });

    } catch (error) {
        console.error('Error in simplification steps:', error);
        steps += '<p>Could not generate detailed steps.</p>';
    }

    steps += '</div>';
    return steps;
}

function formatSolution(solution) {
    if (isDecimalFormat) {
        // Convert to decimal with 6 decimal places
        return nerdamer(solution).evaluate().text('decimals', 6);
    } else {
        // Return as fraction
        return solution.toString();
    }
}

function plotGraph(equation, variable) {
    try {
        // Wait for GeoGebra to be ready
        if (!window.ggbApplet) {
            setTimeout(() => plotGraph(equation, variable), 1000);
            return;
        }

        // Clear previous graphs
        window.ggbApplet.reset();

        if (equation.includes('=')) {
            // For equations like "x^2 + 2x = 4"
            const [left, right] = equation.split('=');
            // Plot left side minus right side = 0
            const rearrangedEquation = `${left}-(${right})`;
            window.ggbApplet.evalCommand(`f(x)=${rearrangedEquation}`);
        } else {
            // For expressions, plot directly
            const cleanEquation = equation
                .replace(/\^{([^}]+)}/g, '^($1)')  // Fix exponent notation
                .replace(/\*/g, '');  // Remove multiplication symbols
            window.ggbApplet.evalCommand(`f(x)=${cleanEquation}`);
        }
        
        // Set viewing window with some padding
        window.ggbApplet.setCoordSystem(-10, 10, -10, 10);
        
        // Set y=0 line for reference with equations
        if (equation.includes('=')) {
            window.ggbApplet.evalCommand("y=0");
            // Intersections will show solutions
            window.ggbApplet.evalCommand("Intersect(f,y=0)");
        }
        
    } catch (error) {
        console.error('GeoGebra plotting error:', error);
        document.getElementById('ggb-element').innerHTML = 
            '<p>Could not generate graph for this equation</p>';
    }
}
function saveToHistory(equation, solution) {
    equationHistory.push({ equation, solution, timestamp: new Date() });
    localStorage.setItem('equationHistory', JSON.stringify(equationHistory));
    renderHistory();
}

function loadHistory() {
    const history = localStorage.getItem('equationHistory');
    if (history) equationHistory = JSON.parse(history);
    renderHistory();
}

function renderHistory() {
    const historyContainer = document.createElement('div');
    historyContainer.className = 'equation-history';
    historyContainer.innerHTML = '<h4>History</h4>';
    
    equationHistory.slice(-5).reverse().forEach(item => {
        const div = document.createElement('div');
        div.className = 'history-item';
        div.innerHTML = `\\[ ${item.equation} \\]  \\[ ${item.solution} \\]`;
        div.onclick = () => {
            currentEquation = item.equation;
            cursorPosition = currentEquation.length;
            updatePreview(currentEquation);
        };
        historyContainer.appendChild(div);
    });
    
    const existing = document.querySelector('.equation-history');
    if (existing) existing.replaceWith(historyContainer);
    else document.querySelector('.container').appendChild(historyContainer);
    
    MathJax.typesetPromise();
}

function toggleSolutionFormat() {
    if (!currentSolution) return;
    
    isDecimalFormat = !isDecimalFormat;
    displaySolution(currentSolution, false); 
}

function clearEquation() {
    currentEquation = '';
    variables.clear();
    updateEquationDisplay();
    document.getElementById('errorMessage').style.display = 'none';
    document.getElementById('solutionSteps').innerHTML = '';
    
    // Clear GeoGebra graph
    if (window.ggbApplet) {
        window.ggbApplet.reset();
    }
}